.viewsreference--view-title {
  @extend .cd-heading-2;
  margin-top: 0;

  .cd-layout-sidebar-first & {
    @extend .cd-heading-3;
    margin-top: 0;
    //margin-top: 1.5rem;
  }
}

.view-updates,
.view-iasc-group-content {
  .view-header {
    @extend .cd-page-title;

    @include xl {
      padding-right: 0;
    }
  }
}

.cd-layout-sidebar-first .views-exposed-form {
  .form--inline {
    margin-bottom: 1rem;
  }

  label {
    @extend .cd-heading-3;
  }
}

.view-iasc-group-content,
.view-search-content,
.view-updates,
.view-resources,
.view-iasc-services {
  .views-exposed-form .cd-form--inline {

    .form-item,
    .form-actions {
      margin: 0;
    }

    label {
      @include visually-hidden;
    }
  }
}

