// Services table
.views-table {
  @media (min-width: 576px) {
    .views-field-field-service-coverage {
      width: 30%;
    }
  }
}

// Adjust height to accommodate height of long th.
@media (max-width: 575px) {
  .cd-table--responsive td.views-field-field-share-data {
    min-height: 8rem;
  }

  .cd-table--responsive td.views-field-field-service-coverage {
    min-height: 4rem;
  }
}

.view-header > p {
  margin-top: 0;
}

.view-iasc-services {
  .view-header h2 {
    @media (max-width: 1023px) {
      margin-bottom: 1rem;
      padding-bottom: 1rem;
      border-bottom: 1px solid $cd-blue-grey;
    }
  }

  .pager {
    margin-bottom: 4rem;

    @include desktop {
      margin-bottom: 6rem;
    }
  }
}

.more-link {
  display: block;
  text-align: right;
}

.view-filters {
  margin-bottom: 2rem;
}
